@import url(https://fonts.googleapis.com/css2?family=Teko:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  text-decoration: none !important;
}

.App{background-color:rgba(0,62,193,0.1);height:100vh;text-align:center;width:100vw}.App .component-container{align-items:center;display:flex;flex-direction:row;justify-content:space-evenly;flex-wrap:wrap;width:100%;height:calc(100% - 50px);position:absolute}.App .current-price{font-weight:500;padding:15px 25px;text-align:left}.App .current-price .price-action{padding-top:5px}.App .current-price .price-header{color:gray;font-size:14px}.App .button-container{padding:15px 25px}.App .timeline-button:hover{color:rgba(0,82,255,0.8)}.App .active-timeline{color:rgba(0,82,255,0.8)}.App .coin-button-container{padding:15px 25px}.App .chart-header-container{display:flex;flex-direction:row;justify-content:space-between}.App .new-coin-input{width:50px}

.navbar-container{background-color:white;border-bottom:1px solid #d1cece;height:68px;position:absolute;width:100%;z-index:100;display:flex;flex-direction:row;justify-content:space-between}.navbar-container .navbar-title{color:rgba(0,82,255,0.8);text-align:left;margin-left:40px !important;padding-top:5px;font-family:"Teko", sans-serif;font-weight:600;font-size:45px;text-decoration:none !important;margin:auto 0;height:100%;letter-spacing:0.02em}.navbar-container .navbar-navigation{display:flex;flex-direction:row;justify-content:space-evenly;width:10%;align-items:center;margin-right:40px;font-weight:500;color:rgba(0,82,255,0.8) !important}.navbar-container .nav-link{color:rgba(0,82,255,0.8)}.navbar-container .active-nav-link,.navbar-container .nav-link:hover{color:rgba(0,82,255,0.9);font-weight:700}.navbar-container .nav-link,.navbar-container .active-nav-link{transition:0.25s}

.coin-list-container{height:"500px";width:"500px";position:"absolute";display:flex;flex-direction:row;width:100vw;flex-wrap:wrap;height:calc(100vh - 50px);top:50px;overflow:scroll;justify-content:space-evenly;padding:25px 0px}.coin-list-container .individual-coin{padding:5px}

.error-container{height:50px;width:200px;background-color:#dd3535;color:white;font-weight:700;border-radius:7px;position:absolute;right:15px;top:10px;z-index:100;-webkit-animation:4s warning linear;animation:4s warning linear}@-webkit-keyframes warning{0%{opacity:0;top:40px}10%{opacity:1;top:10px}80%{opacity:1}100%{opacity:0}}@keyframes warning{0%{opacity:0;top:40px}10%{opacity:1;top:10px}80%{opacity:1}100%{opacity:0}}

.news-container{background-color:white;width:300px;border:1px solid #d1cece}.for-you{color:rgba(0,82,255,0.8);font-size:25px;font-weight:700}.view-more{font-size:20px;padding:10px;background-color:rgba(0,82,255,0.8);color:white;font-weight:500}.timeline-button{border:none;background-color:white;color:#b9b9b9;font-weight:700;cursor:pointer;transition:0.35s}.timeline-button:hover{color:rgba(0,82,255,0.8)}.active-timeline{color:rgba(0,82,255,0.8)}.coin-button-container{padding:15px 25px;display:flex;flex-direction:row;justify-content:flex-start;align-items:flex-start}.chart-header-container{display:flex;flex-direction:row;justify-content:space-between}.new-coin-input{width:60px !important;font-weight:700;border:none;outline:none;padding:0px 1px;border-bottom:1px solid rgba(0,82,255,0.8)}.new-coin-input::-webkit-input-placeholder{color:#b9b9b9}.new-coin-input:-ms-input-placeholder{color:#b9b9b9}.new-coin-input::placeholder{color:#b9b9b9}.new-coin-input:focus{color:rgba(0,82,255,0.8)}.coin-list-item,.browse-all{margin:0;padding:0 5px;transition:0.25s;cursor:pointer;border:none;width:100%;text-align:left;background-color:transparent}.browse-all{white-space:nowrap;background-color:rgba(0,82,255,0.8);color:white;padding:5px;margin-top:5px;border-radius:5px}.coin-list-item:hover{background-color:rgba(0,82,255,0.15)}.coin-search-container{width:150px;font-size:14px;position:absolute;background-color:rgba(185,185,185,0.3)}.remove-button{color:#dd3535 !important}.positive{color:#13a313}.negative{color:#dd3535}.percentage{padding-top:5px;font-weight:400;font-size:15px;padding-left:10px}.input-container{margin-top:-2px;margin-left:4px}.coin-chart-header{color:rgba(0,82,255,0.8);font-weight:700;font-size:25px}.line-container{background-color:white;width:100%;height:100%;border:1px solid #d1cece;margin-left:-1px;border-top:none}.chart-wrapper-container{width:70vw;height:30vw;background-color:white;border:1px solid #d1cece}

.news-item-container{display:flex;flex-direction:row;align-items:center;text-decoration:none;justify-content:flex-start;color:black;text-align:left;padding:0px 10px;transition:0.25s}.news-item-container .news-image{padding-right:10px;width:50px;height:50px}.news-item-container .news-title{font-weight:500;font-size:13px;text-transform:full-width;letter-spacing:0.05em;line-height:1.1rem}.news-item-container:hover{background-color:#e9e7e7}.news-container,.news-container2{background-color:#fff;width:300px;display:flex;flex-direction:column;border:1px solid #d1cece}.news-container2{width:90%;overflow:scroll;height:calc(100vh - 150px);margin-top:50px}.for-you{color:rgba(0,82,255,0.8);height:50px;font-size:25px;font-weight:700;padding:10px 0}.news-parent-container{height:30vw;display:flex;flex-direction:column;justify-content:space-between;margin-top:5px}.view-more{font-size:20px;padding:10px;background-color:rgba(0,82,255,0.8);color:white;font-weight:500;text-decoration:none}

